* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-family: Arial, sans-serif;
	background-color: #030303;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100vh;
}

.widget {
	width: 720px;
	height: 100vh;
	background: #fff;
	border-radius: 10px;
	box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
	display: flex;
	flex-direction: column;
	overflow: hidden;
}

/* List messages */
.list-message {
	flex: 1;
	overflow-y: auto;
	padding: 10px;
	background: #f9f9f9;
	border-bottom: 1px solid #ddd;
}

.list-message::-webkit-scrollbar {
	width: 6px;
}

.list-message::-webkit-scrollbar-thumb {
	background: #ccc;
	border-radius: 3px;
}

/* Message container */
.message-container {
	margin-bottom: 15px;
	border-radius: 8px;
	padding: 10px;
	background: #e0e0e0;
	display: flex;
	flex-direction: column;
	width: 100%;
	max-width: 700px;
	position: relative;
}

.message-container .data {
	font-size: 14px;
	color: #333;
	width: 75%;
	margin-bottom: 15px;
}

.message-container .date {
	font-size: 12px;
	color: #888;
	position: absolute;
	top: 10px;
	right: 10px;
}

.message-container .geolocation {
	font-size: 12px;
	color: #666;
	text-align: left;
}

.message-container audio,
.message-container video {
	width: 100%;
	object-fit: contain;
	margin: 25px 0 10px;
}

.audio-player {
	width: 75%;
}

/* Create message input */
.create-message {
	display: flex;
	align-items: center;
	padding: 10px;
	background: #fff;
}

.enter-text {
	flex: 1;
	padding: 8px 10px;
	border: 1px solid #ddd;
	border-radius: 8px;
	resize: none;
	font-size: 14px;
	font-family: inherit;
}

/* Icons */
.fa-solid {
	padding: 10px;
	font-size: 30px;
	color: #555;
	cursor: pointer;
	transition: color 0.3s;
}

.fa-microphone:hover,
.fa-video:hover,
.confirm-record:hover,
.cancel-record:hover {
	color: #000;
}

.icon {
	display: none;
}

/* Timer */
.timer {
	padding: 15px;
	width: 71px;
	text-align: center;
}

/* Modal */
.modal {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.4);
	display: flex;
	justify-content: center;
	align-items: center;
	opacity: 0;
	visibility: hidden;
	transition: opacity 0.3s ease, visibility 0.3s ease;
}

.modal.active {
	opacity: 1;
	visibility: visible;
}

.modal-content {
	background: #fff;
	border-radius: 10px;
	padding: 20px;
	width: 90%;
	max-width: 400px;
	box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
	text-align: center;
}

.modal-content h3 {
	font-size: 18px;
	margin-bottom: 15px;
}

.modal-content p {
	font-size: 14px;
	color: #666;
	margin-bottom: 15px;
}

.modal-content input {
	width: 100%;
	padding: 10px;
	font-size: 14px;
	border: 1px solid #ddd;
	border-radius: 8px;
	margin-bottom: 15px;
}

.modal-content .buttons {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.modal-content .buttons button {
	padding: 8px 15px;
	font-size: 14px;
	border: none;
	border-radius: 8px;
	cursor: pointer;
	transition: background 0.3s;
}

.modal-content .buttons button.cancel {
	background: #f5f5f5;
	color: #666;
}

.modal-content .buttons button.ok {
	background: #007bff;
	color: #fff;
}
